import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';

import PubSub, { topics } from '../../lib/pubsub';

import Images from '../../components/form/images';
import DogHistory from '../../components/dog/history';
import Modal from '../../components/modal';
import LineGraph from '../../components/lineGraph';

const MONTHS = ['jan', 'feb', 'mar', 'apr', 'mai', 'jun', 'jul', 'aug', 'sep', 'okt', 'nov', 'des'];

function byDate(a, b) {
	if (a.date < b.date){
		return -1;
	}
	if (a.date > b.date){
		return 1;
	}
	return 0;
}

const initialState = {
	showLog: false,
	okToAddLog: true,
};

@observer
class DogLog extends Component {
	constructor(props) {
        super(props);
        this.state = {
			newHistory: {
				date: util.isoDate(new Date(), false, false, true),
				time: util.isoTime(new Date()),
			},
			...initialState,
        };
    }

	async loadDog() {
		const { dogStore, userStore } = this.props.stores;
        await dogStore.load(this.props.dog.id);
		const { dog } = dogStore;
        await userStore.getWorkoutSummary(null, dog.id);
    }

	toggleLog = () => {
		const { showLog } = this.state;
		this.setState({
            showLog: !showLog,
        });
	}

	resetLog = () => {
		this.setState({
			newHistory: {
				date: util.isoDate(new Date(), false, false, true),
				time: util.isoTime(new Date()),
			},
			showLog: false,
		});
	}

	addImages = (obj) => {
		// updateField({ id: object.id, field, value: file });
		// console.log('addImages', obj.value);
		if (Array.isArray(obj.value)) {
			const { newHistory } = this.state;
			if (Array.isArray(newHistory.images)) {
				newHistory.images = [...newHistory.images, ...obj.value];
			} else {
				newHistory.images = obj.value;
			}
			this.setState({ newHistory });
		}
	}

	handleImageErrored = (e) => {
        const image = e.target;
        image.onerror = null;
        setTimeout(() => {
            image.src += `?${new Date()}`;
        }, 1000);
    }

	imageUploadStatus = (status) => {
		this.setState({ okToAddLog: status });
	}

	insertLog = async () => {
		const { newHistory } = this.state;
		const { dogStore, userStore } = this.props.stores;
		const { dog } = dogStore;
		const { user } = userStore;

		if (!newHistory.title && !newHistory.comment) {
			PubSub.publish(topics.STATUS_MESSAGE, {
				message: 'Mangler tittel p logginnlegg',
				type: 'warning',
				icon: 'fas fa-exclamation-triangle',
			});
			return false;
		}

		newHistory.date = `${newHistory.date} ${newHistory.time}`;
		
		await dogStore.save({
			id: dog.id,
			history: {
				user: {
					id: user.id,
					email: user.email,
					firstname: user.firstname,
					lastname: user.lastname,
				},
				...newHistory,
			},
		});
        this.resetLog();
		this.loadDog();
	}

	render() {
		const { newHistory, showLog, okToAddLog } = this.state;
		const { dog } = this.props;

		if (showLog) {
			return (
				<div class='col-12 px-0'>
					<button class='btn btn-sm btn-secondary float-left'
						onClick={this.toggleLog}
					>
						<i class='fas fa-plus' /> {showLog ? 'Skjul logg' : 'Ny logg'}
					</button>
					<br />

					{showLog && <div class='border-bottom mb-2 py-4 px-5'>
						<h5>Legg til logg: {dog.name}</h5>
						<div class='row'>
							<div class='form-group col'>
								<label for='dateInput'><i class='fas fa-calendar-alt' /> Dato</label>
								<input type='date' class='form-control' id='dateInput' aria-describedby='dateHelp'
									onInput={linkState(this, 'newHistory.date')}
									value={newHistory.date}
									autocomplete='off'
								/>
								<small id='dateHelp' class='form-text text-muted'>Dato for logglinje.</small>
							</div>
							<div class='form-group col'>
								<label for='timeInput'><i class='fas fa-clock' /> Klokka</label>
								<input type='time' class='form-control' id='timeInput' aria-describedby='timeHelp'
									onInput={linkState(this, 'newHistory.time')}
									value={newHistory.time}
									autocomplete='off'
								/>
								<small id='timeHelp' class='form-text text-muted'>Tid for logglinje.</small>
							</div>
						</div>
						<div class='form-group'>
							<label for='nameInput'>Tittel</label>
							<input type='text' class='form-control' id='nameInput' aria-describedby='nameHelp'
								onInput={linkState(this, 'newHistory.title')}
								value={newHistory.title}
								autocomplete='off'
								required={true}
							/>
							<small id='nameHelp' class='form-text text-muted'>Tittel p logglinje. Vetrinr besk, Vaksinering, Syk, Profilbilde, etc.</small>
						</div>
						<div class='form-group'>
							<label for='typeInput'><i class='fas fa-folder-open' /> Type hendelse</label>
							<select class='form-control' id='typeInput' aria-describedby='typeHelp'
								onInput={linkState(this, 'newHistory.historyType')}
								value={newHistory.historyType}
								autocomplete='off'
							>
								<option value=''>-- Velg type --</option>
								<option value='other'>Annet</option>
								<option value='maturity'>Lpetid</option>
								<option value='update'>Oppdatering</option>
								<option value='injury'>Skade</option>
								<option value='fight'>Slsskamp</option>
								<option value='sick'>Syk</option>
								<option value='vaccine'>Vaksine</option>
								<option value='weight'>Veiing</option>
								<option value='veterinarianVisit'>Vetrinr besk</option>
								<option value='newStraw'>Ny halm/treull</option>
								<option value='clawClip'>Kloklipp</option>
							</select>
							<small id='nameHelp' class='form-text text-muted'>Type hendelse. Vetrinr besk, vaksinering, syk, lpetid, etc.</small>
						</div>
						{newHistory.historyType === 'weight' && <div class='form-group'>
							<label for='weightInput'><i class='fas fa-balance-scale' /> Vekt</label>
							<input type='text' class='form-control' id='weightInput' aria-describedby='weightHelp'
								onInput={linkState(this, 'newHistory.weight')}
								value={newHistory.weight}
								autocomplete='off'
							/>
							<small id='weightHelp' class='form-text text-muted'>Vekten til hunden.</small>
						</div>}
						<div class='form-group'>
							<label for='commentInput'><i class='fas fa-quote-right' /> Innhold</label>
							<input type='text' class='form-control' id='commentInput' aria-describedby='commentHelp'
								onInput={linkState(this, 'newHistory.comment')}
								value={newHistory.comment}
								autocomplete='off'
							/>
							<small id='commentHelp' class='form-text text-muted'>Innholdet til loggen. Skriv mer om hva som hendte.</small>
						</div>
						{(newHistory.historyType === 'veterinarianVisit' || newHistory.historyType === 'vaccine') && <div class='form-group'>
							<label for='costInput'><i class='fas fa-money-bill-alt' /> Kostnad</label>
							<input type='text' class='form-control' id='costInput' aria-describedby='costHelp'
								onInput={linkState(this, 'newHistory.cost')}
								value={newHistory.cost}
								autocomplete='off'
								pattern='[0-9]{3}' title='Kun tall. Ikke noen andre tegn eller symboler.'
							/>
							<small id='costHelp' class='form-text text-muted'>Kostnad for denne hendelsen.</small>
						</div>}

						<div class='container-fluid'>
							<div class='row'>
								<div class='row-12'>
									<label for='imageInput'><i class='fas fa-camera' /> Bilder</label>
								</div>
							</div>
							<div class='row'>
								{newHistory.images && newHistory.images.map(img => (
									<div class='col-3 p-1'>
										<img src={img.s3SmallLink} class='img-fluid' onError={this.handleImageErrored} />
									</div>
								))}
							</div>
						</div>
						<Images object={newHistory} field='images' updateField={this.addImages} uploadStatus={this.imageUploadStatus} />

						<button type='button' class='btn btn-success'
							onClick={this.insertLog}
							disabled={!okToAddLog}
						>
							{okToAddLog ? 'Legg til logg' : 'vent litt...'}
						</button>
						<button type='button' class='btn btn-danger float-right'
							onClick={this.resetLog}
						>
							Nullstill
						</button>
					</div>}

				</div>
			);
		}

		return (
			<button class='btn btn-sm btn-secondary float-left'
				onClick={this.toggleLog}
			>
				<i class='fas fa-plus' /> {showLog ? 'Skjul logg' : 'Ny logg'}
			</button>
		);
	}
}

@observer
class DogsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
			newHistory: {
				date: util.isoDate(new Date(), false, false, true),
				time: util.isoTime(new Date()),
			},
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadDog() {
		const { dogStore, userStore } = this.props.stores;
        await dogStore.load(this.props.dog);
		const { dog } = dogStore;
        await userStore.getWorkoutSummary(null, dog.id);
    }

	
	showModal = (e) => {
        e.stopPropagation();
        e.preventDefault();
        const img = e.target.dataset.image;
        const imgXXL = e.target.dataset.imagexxl;
		const thumb = e.target.dataset.thumb;
        const name = e.target.dataset.name;
        const title = e.target.dataset.title;
        const date = e.target.dataset.date;
        this.setState({
            displayModal: true,
            currentImage: img,
            currentImageXXL: imgXXL,
            thumb,
            name,
            title,
            date,
        });
    }

    closeModal = () => {
        this.setState({
            displayModal: false,
        });
    }

    back = () => {
        route(`/dogs/`, true);
    }

    componentWillMount() {
        this.loadDog();
    }

	render() {
		const { displayModal, currentImage, currentImageXXL, thumb, name, title, date } = this.state;
		const { dogStore, userStore } = this.props.stores;
		const { dog } = dogStore;
		const {
            graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev,
			graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev,
        } = userStore;
		const hasHistory = dog.history && dog.history.length > 0;
        const workoutSummary2019 = dogStore.findWorkoutSummary(dog.id, 2019) || {};
        const workoutSummary2020 = dogStore.findWorkoutSummary(dog.id, 2020) || {};
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link' onClick={this.back}>
                            <i class='fas fa-arrow-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>Hund</h5>
                    </div>
                    <div>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                    </div>
                </div>

				{displayModal && <Modal close={this.closeModal} image={currentImage} imagexxl={currentImageXXL} thumb={thumb} name={name} title={title} date={date} />}

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-2'>
						<DogLog stores={this.props.stores} dog={dog} />
						<a class='btn btn-sm btn-primary float-right'
							href={`/dogs/edit/${dog.id}`}
						>
							<i class='fas fa-edit' /> Endre {dog.name}
						</a>
					</div>


					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<div class='d-flex mb-2'>
                            {/* {JSON.stringify(dog, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={dog.image ? `background-image: url("${dog.image.s3SmallLink}");` : ''}>
                                {!dog.image && <i class='fas fa-dog text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3 pr-2'
								style={`border-right: 4px solid ${userStore.findHarness(dog.harness, 'colorNonstop')} !important;`}
							>
								<span class='float-right'>
									{dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)}</span>}
									{dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
								</span>
								<h5>{dog.name}</h5>
								{dog.shortname}<br />

								<div>
									{dog.chipId && <small><i class='fas fa-microchip' /> {dog.chipId}</small>}
								</div>
                            </div>
                        </div>


						<div class='w-100 mt-3 mb-4'>
                            <small class='mb-4'><small>
                                <table class='table table-sm font-weigth-lighter border-bottom'>
                                    <thead>
                                        <tr>
                                            <th style='padding: .2rem;'>Mnd</th>
                                            {util.range(1, 12).map(w => (
                                                <th class='text-right' style='padding: .2rem;'>{MONTHS[w - 1]}</th>
                                            ))}
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <th scope='row' style='padding: .2rem;'>2019</th>
                                            {util.range(1, 12).map(m => (
                                                <>
                                                    <td class='text-right' style='padding: .2rem;'>
                                                        {userStore.findWorkoutSummaryMonth(2019, m)}<br />
                                                        {userStore.findWorkoutSummaryMonth(2019, m, 'elevation')}<br />
                                                    </td>
                                                </>
                                            ))}
                                            <td class='text-muted' style='padding: .2rem;'>
                                                <nobr><i class='fas fa-road' /> km</nobr><br />
                                                <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th scope='row' style='padding: .2rem;'>2020</th>
                                            {util.range(1, 12).map(m => (
                                                <>
                                                    <td class='text-right' style='padding: .2rem;'>
                                                        {userStore.findWorkoutSummaryMonth(2020, m)}<br />
                                                        {userStore.findWorkoutSummaryMonth(2020, m, 'elevation')}<br />
                                                    </td>
                                                </>
                                            ))}
                                            <td class='text-muted' style='padding: .2rem;'>
                                                <nobr><i class='fas fa-road' /> km</nobr><br />
                                                <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </small></small>
                                        
							<LineGraph
								stores={this.props.stores}
								width={500}
								height={150}
								showXTicks={1}
								xTicksVal={val => `uke ${val}`}
								paddingTop={30}
								lineFromXzero={true}
								lineFromXmax={true}


								dataLeft={[graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev]}
								legendLeft={['2020 Distanse', '2019 Distanse']}
								// yMaxLeft={30}
								// yMinLeft={0}
								showYTicksLeft={1}
								yTicksLeftPostfix={'km'}
								unitLeft={['km', 'km']}
								avgWindowLeft={[0, 0]}
								smoothLeft={[0, 0]}

								dataRight={[graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev]}
								legendRight={['2020 Hydemeter', '2019 Hydemeter']}
								showYTicksRight={1}
								yTicksRightPostfix={'m'}
								unitRight={['m', 'm']}
								avgWindowRight={[0, 0]}
								smoothRight={[0, 0]}
							/>
						{graphWorkoutSummaryWeekDistanceCurrent && graphWorkoutSummaryWeekDistanceCurrent.length < 2 && (
							<div class='mb-5 py-3 border-top border-bottom'>
								<div class='text-left' style='line-height: 1.0em;'>
									<div class='display-4 float-left text-warning mr-3'>
										<i class='fas fa-paw' />
									</div>
									<small>
										Forelpig er det litt lite data for  kunne vise noen fine grafer. 
										Etterhvert som du legger inn flere treninger s vil dette bli en fin tabell og graf som viser progresjonen din.
									</small>
								</div>
							</div>
						)}

                        </div>


						<div class='container-fluid mb-2'>
							<div class='row mt-2 border-top'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-road' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.distanceKm || 0, 0)} km
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-road' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.distanceKm || 0, 0)} km
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.elevation || 0, 0)} m
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.elevation || 0, 0)} m
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-clock' /> 2020-21</small>
                                    <h3>
										{util.secToHms(workoutSummary2020.duration || 0)}
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-clock' /> 2019-20</small>
                                    <h3>
                                        {util.secToHms(workoutSummary2019.duration || 0)}
                                    </h3>
                                </div>
                            </div>
							<div class='row mt-2 border-bottom'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-tachometer-alt' /> 2020-21</small>
                                    <h3>
										{util.format(workoutSummary2020.speedAvg || 0, 1)} km/t
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'><i class='fas fa-tachometer-alt' /> 2019-20</small>
                                    <h3>
                                        {util.format(workoutSummary2019.speedAvg || 0, 1)} km/t
                                    </h3>
                                </div>
                            </div>

	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Fdt</small>
                                    <h3>
                                        {util.isoDate(dog.birth, false, false, true)}
                                    </h3>
                                </div>
                                {dog.deceased && <div class='col text-center border-left'>
                                    <small class='text-muted'>Dd</small>
                                    <h3>
                                        {util.isoDate(dog.deceased, false, false, true)}
                                    </h3>
                                </div>}
                            </div>
	                        <div class='row mt-2'>
								<div class='col text-center'>
                                    <small class='text-muted'>Team</small>
                                    <h3>
                                        {dog.team && userStore.findTeam(dog.team)}
                                    </h3>
                                </div>
							</div>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Mor</small>
                                    <h3>
                                        {dog.mother}
                                    </h3>
                                </div>
                                <div class='col text-center border-left'>
                                    <small class='text-muted'>Far</small>
                                    <h3>
                                        {dog.father}
                                    </h3>
                                </div>
                            </div>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'>Ssken</small>
                                    <h3>
                                        {dog.siblings}
                                    </h3>
                                </div>
                            </div>
                        </div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<div class='container-fluid border-bottom pb-4'>
							<div class='row'>
								<div class='col px-0'>
									<h5>Bilder av {dog.name}</h5>
								</div>
							</div>
							<div class='row'>
								{dog.images && dog.images.map(img => (
									<div class={`text-center border rounded imageRounded`} style={img ? `background-image: url("${img.s3SmallLink}");` : ''}
										onClick={this.showModal}
										data-image={img.s3LargeLink}
										data-imagexxl={img.s3XXLargeLink}
										data-thumb={dog.image.s3SmallLink}
										data-name={dog.name}
									>					
										&nbsp;
									</div>
								))}
							</div>
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-2 mt-3'>
						<DogLog stores={this.props.stores} dog={dog} />
						<a class='btn btn-sm btn-primary float-right'
							href={`/dogs/edit/${dog.id}`}
						>
							<i class='fas fa-edit' /> Endre {dog.name}
						</a>
					</div>

					<div class='offset-0 col-12'>
						<div class='container-fluid'>
							<div class='row'>
								<div class='col-12'>
									<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
										<h5 class='mt-3'>Logg for {dog.name}</h5>
									</div>
									{!hasHistory && <div class='text-muted'>Loggen til {dog.name} er tom</div>}
									{hasHistory && dog.history.sort(byDate).reverse().map(line => (
										<DogHistory stores={this.props.stores} line={line} />
									))}							
								</div>
							</div>
						</div>

					</div>
				</div>
			</div>
		);
	}
}

export default DogsDetail;
