import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import linkState from 'linkstate';
import { route } from 'preact-router';

import DatePicker from '../../components/form/datePicker';
// import TimePicker from '../../components/form/timePicker';
import Images from '../../components/form/images';

const initialState = {
    newWorkout: {},
};

const DAY_OF_WEEK = [
    'sndag',
    'mandag',
    'tirsdag',
    'onsdag',
    'torsdag',
    'fredag',
    'lrdag',
];

@observer
class InputNew extends Component {
    onInput = (e) => {
        const { field } = this.props;
        const value = e.target.value;
        const { workoutStore } = this.props.stores;
        workoutStore.updateObjectKeyValue('newWorkout', field, value);
    }

    selectDate = (value) => {
        const { field } = this.props;
        const { workoutStore } = this.props.stores;
        workoutStore.updateObjectKeyValue('newWorkout', field, util.isoDate(value));
    }

    render() {
        const { type, datalist = [], min, max, step, elementtype, field, title, icon, help } = this.props;
        const { workoutStore } = this.props.stores;
        const { newWorkout } = workoutStore;
        let maxValue = max;
        if (type === 'date') {
            if (max === 'today') {
                maxValue = util.isoDate(undefined, false, false, true);
            }
        }
        let displayValue = newWorkout[field];
        if (field === 'duration') {
            displayValue = util.secToHms(newWorkout[field], true);
        }
        if (type === 'datalist') {
            return (
                <div class={`form-group mb-4 pb-1 border-bottom`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                    <input list={`list-${field}`} class={`form-control`} id={`${field}Input`} aria-describedby={`${field}Help`}
                        onInput={this.onInput}
                        value={newWorkout[field]}
                    />
                    <datalist id={`list-${field}`} >
                        {datalist.map(el => (
                            <option value={el[0]}>{el[1]}</option>
                        ))}
                    </datalist>
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>
                </div>
            );
        }
        return (
            <div class='form-group mb-4 pb-1 border-bottom'>
                <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                {type === 'range' && <span class='float-right'>
                    <strong>{displayValue}</strong>
                </span>}
                <input type={type} class={`form-control`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    min={min}
                    max={maxValue}
                    step={step}
                    onInput={this.onInput}
                    value={newWorkout[field]}
                />
                <small id={`${field}Help`} class='form-text text-muted'>{help}</small>
            </div>
        );
    }
}

@observer
class Input extends Component {
    selectDate = (value) => {
        const { workout, type, field, updateField } = this.props;
        if (workout.id) {
            updateField({ id: workout.id, field, value, type });
        }
    }

    handleImageErrored = (e) => {
        const image = e.target;
        image.onerror = null;
        setTimeout(() => {
            image.src += `?${new Date()}`;
        }, 1000);
    }

    render() {
        const { saved, workout, type, datalist = [], min, max, step, elementtype, field, title, icon, help, onInput, updateField } = this.props;
        let value = toJS(workout[field]);
        let maxValue = max;
        if (type === 'date') {
            if (value) {
                value = util.isoDate(workout[field], false, false, true);
            }
            if (max === 'today') {
                maxValue = util.isoDate(undefined, false, false, true);
            }
        }
        let displayValue = value;
        if (field === 'duration') {
            displayValue = util.secToHms(value, true);
        }

        if (type === 'image') {
            return (
                <div class={`form-group  mb-4 pb-1 border-bottom ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>

                    <div class='container-fluid'>
                        <div class='row'>
                            {workout.images && workout.images.map(img => (
                                <div class='col-3 p-1'>
                                    <img src={img.s3SmallLink} class='img-fluid' onError={this.handleImageErrored} />
                                </div>
                            ))}
                        </div>
                    </div>
                    <Images saved={saved} object={workout} field={field} updateField={updateField} />
                    {saved[`${field}.${workout.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'datalist') {
            return (
                <div class={`form-group  mb-4 pb-1 border-bottom ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                    <input list={`list-${field}`} class={`form-control ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                        value={value}
                        data-id={workout.id}
                        data-field={field}
                        onInput={onInput}
                    />
                    <datalist id={`list-${field}`} >
                        {datalist.map(el => (
                            <option value={el[0]}>{el[1]}</option>
                        ))}
                    </datalist>
                    {saved[`${field}.${workout.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }

        return (
            <div class={`form-group  mb-4 pb-1 border-bottom ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                {type === 'range' && <span class='float-right'>
                    <strong>{displayValue}</strong>
                </span>}
                <input type={type} class={`form-control ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    min={min}
                    max={maxValue}
                    step={step}
                    value={value}
                    data-id={workout.id}
                    data-field={field}
                    data-type={type}
                    data-elementtype={elementtype}
                    onInput={onInput}
                />
                {saved[`${field}.${workout.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

@observer
class Dogs extends Component {
    toggleDog(dogId) {
        const { workout, type, elementtype, field, updateField, isNew } = this.props;
        const value = toJS(workout[field]) || [];
        const idx = value.indexOf(dogId);
        if (idx > -1) {
            value.splice(idx, 1);
        } else {
            value.push(dogId);
        }
        updateField({ id: workout.id, field, value, type, elementtype, isNew });
    }

    toggleFilter = () => {
        const { showAll } = this.state;
        this.setState({
            showAll: showAll ? 0 : 1,
        });
    }

    render() {
        const { showAll } = this.state;
        const { userStore, dogStore } = this.props.stores;
		const { dogs } = dogStore;
        const { saved, workout, field, title, icon, help, filter = () => true } = this.props;
        const value = toJS(workout[field]) || [];
        const dogFilter = showAll ? () => true : filter;
        // console.log('Dogs.render: type, elementType, value:', type, elementtype, value);
        return (
            <div class={`form-group  mb-4 pb-1 border-bottom ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                <div class='container-fluid px-0'>
                    <div class='row'>
                        {dogs && dogs.filter(dogFilter).map(dog => (
                            <div class='col-6 col-sm-6 col-md-6 col-lg-6 mb-1'>
                                <div class={`border rounded px-3 pt-2 pb-1 ${value.indexOf(dog.id) > -1 ? 'bg-success' : ''} d-flex`}
                                    onClick={() => this.toggleDog(dog.id)}
                                    style='padding: .3rem !important; line-height: 1.1em; cursor: pointer; pointer-events: inherit;'
                                >

                                    <div class={`text-center border rounded-circle imageRounded imageRoundedSmall`} style={`${dog.image ? `background-image: url("${dog.image.s3ThumbLink}");` : ''}`}>
                                        {!dog.image && <i class='fas fa-dog mt-1' style='font-size: 25px;' />}
                                    </div>
                                    <div class='flex-grow-1 pl-2 pr-1'
                                        style={`border-right: 4px solid ${userStore.findHarness(dog.harness, 'colorNonstop')} !important; line-height: 0.9em;`}
                                    >
                                        <small>
                                            {/* {dog.image ? <img src={dog.image.s3SmallLink} class='img-fluid rounded-circle mr-2' style='width: 25px; height: 25px;' /> : <i class='fas fa-dog mr-2' />} */}
                                            <small class='float-right'>
                                                {dog.birth && <span class='mr-1'>{util.age(dog.birth, dog.deceased)}</span>}
                                                {dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
                                            </small>
                                            {dog.shortname || dog.name}<br />
                                            <div style='line-height: 0.8em;'><small>{userStore.findTeam(dog.team)}</small></div>
                                        </small>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                    <div class='row'>
                        <div class='col-12 text-right'>
                            {showAll ?
                                <button class='btn btn-sm btn-link' onClick={this.toggleFilter}>Vis kun hundene i teamet og de som er valgt</button> :
                                <button class='btn btn-sm btn-link' onClick={this.toggleFilter}>Vis alle hundene</button>
                            }
                        </div>
                    </div>
                </div>
                {saved[`${field}.${workout.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

