@observer
class WorkoutsEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    loadWorkout = async (team) => {
		const { workoutStore, userStore } = this.props.stores;
        let { workouts = [] } = workoutStore;
        const { team: currentTeam } = userStore;
        if (this.props.workout === 'new') {
            if (team) {
                workouts = await workoutStore.load({ query: { team }, skipUpdate: 1, limit: 2 });
            }
            let lastWorkout = workouts[0];
            this.setState({ createNew: true });
            if (lastWorkout) {
                workoutStore.updateObjectKeyValue('newWorkout', 'dogs', lastWorkout.dogs);
                workoutStore.updateObjectKeyValue('newWorkout', 'mushers', lastWorkout.mushers);
                workoutStore.updateObjectKeyValue('newWorkout', 'temperature', lastWorkout.temperature || 5);
                workoutStore.updateObjectKeyValue('newWorkout', 'type', lastWorkout.type);
                workoutStore.updateObjectKeyValue('newWorkout', 'equipment', lastWorkout.equipment);
                workoutStore.updateObjectKeyValue('newWorkout', 'duration', lastWorkout.duration);
                workoutStore.updateObjectKeyValue('newWorkout', 'rating', lastWorkout.rating || 3);
                workoutStore.updateObjectKeyValue('newWorkout', 'date', util.isoDate(lastWorkout.date, false,  false, true));
            } else {
                const date = new Date();
                const dayOfWeek = DAY_OF_WEEK[date.getDay()];
                workoutStore.updateObjectKeyValue('newWorkout', 'name', util.ucfirst(`${dayOfWeek}stur med hundene`));
                workoutStore.updateObjectKeyValue('newWorkout', 'temperature', 5);
                workoutStore.updateObjectKeyValue('newWorkout', 'type', 1);
                // workoutStore.updateObjectKeyValue('newWorkout', 'team', currentTeam.id);
                workoutStore.updateObjectKeyValue('newWorkout', 'equipment', 2);
                workoutStore.updateObjectKeyValue('newWorkout', 'duration', 3600);
                workoutStore.updateObjectKeyValue('newWorkout', 'rating', 3);
                workoutStore.updateObjectKeyValue('newWorkout', 'date', util.isoDate(undefined, false,  false, true));
            }
        } else {
            workoutStore.load(this.props.workout);
        }
    }

    loadDogs = async () => {
		this.setState({ isLoading: true });
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
		this.setState({ isLoading: false });
	}

	resetDog() {
        const { workoutStore } = this.props.stores;
        workoutStore.updateKeyValue('newWorkout', {});
	}

	insertWorkout = async () => {
		const { workoutStore, userStore } = this.props.stores;
		const { newWorkout } = workoutStore;
		await workoutStore.insert({ ...newWorkout });
        this.resetDog();
        userStore.getInfo();
        route(`/workouts/`, true);
	}

    updateField = (e) => {
        const { id, field, type, elementtype, isNew } = e.target.dataset;
        const value = e.target.value;
        this.updateFieldValue({ id: parseInt(id, 10), field, value, type, elementtype, isNew });
    }

    updateFieldValue = ({ id, field, value, datatype, elementtype, isNew = false, skipTimer = false }) => {
        const { workoutStore } = this.props.stores;
        let newValue = value;

        let localObjectValue = value;
        const { workout } = workoutStore;
        if (field === 'images') {
            if (Array.isArray(workout.images) && Array.isArray(value)) {
                localObjectValue = [...workout.images, ...value];
            }
        }

        if (datatype === 'array' && !Array.isArray(value)) {
            if (elementtype === 'number') {
                newValue = value.split(',').map(e => parseFloat(e) || e);
            } else  {
                newValue = value.split(',');
            }
            // console.log('updateFieldValue', id, field, value, datatype, elementtype);
        }

        if (isNew) {
            workoutStore.updateObjectKeyValue('newWorkout', field, value);
        } else {
            workoutStore.updateField(id, field, localObjectValue);
            if (skipTimer) {
                this.doUpdateField(id, field, newValue);
            } else {
                clearTimeout(this.updateTimer[field]);
                this.updateTimer[field] = setTimeout(async () => {
                    this.doUpdateField(id, field, newValue);
                }, 500);
            }
        }
    }

    async doUpdateField(id, field, value) {
        const { workoutStore } = this.props.stores;
        await workoutStore.saveField(id, field, value);
    }

    back = () => {
        const { workoutStore } = this.props.stores;
        const { workout } = workoutStore;
        if (workout.id) {
            route(`/workouts/${workout.id}`, true);
        } else {
            route(`/workouts/`, true);
        }
    }

    componentWillMount() {
        this.loadWorkout();
        this.loadDogs();
    }

	render() {
        const { createNew } = this.state;
		const { workoutStore } = this.props.stores;
		const { insertStatus, saved, workout, newWorkout, workoutLocations, isTracking } = workoutStore;
        const { name, date } = workout;
		return (
			<div class='container-fluid' style='margin-bottom: 200px; margin-top: 60px;'>
                <button class='btn btn-link float-left' onClick={this.back}>
                    <i class='fas fa-arrow-left' />
                </button>
				<div class='row'>
					<div class='offset-0 col-12 offset-sm-1 col-sm-10 offset-md-2 col-md-8 offset-lg-3 col-lg-6'>
                        {createNew ? (
                            <div> 
                                <h5>Legg til ny treningstur</h5>
                                <Teams stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='number' field='team' icon='fas fa-users' title='Team' help='Teamet som er p tur.' updateField={this.updateFieldValue} selectTeam={this.loadWorkout} />

                                <InputNew stores={this.props.stores} field='date' icon='fas fa-clock' type='date' min='2010-01-01' max='today' title='Dato' help='Dato for turen. Format p dato er: "YYYY-MM-DD".' />
                                <Types stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='number' field='type' icon='fas fa-running' title='Type tur' help='Type treningstur. "trening", "konkurranse", "tur".' updateField={this.updateFieldValue} />
                                <Tracks stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='number' field='track' icon='fas fa-map-marked' title='Runde' help='Hvilken treningsrunde er dette.' updateField={this.updateFieldValue} />

                                <InputNew stores={this.props.stores} field='name' icon='fas fa-heading' title='Tittel' help='Tittel p denne turen. "Fin hsttur i sende regn..."' />
                                <InputNew stores={this.props.stores} field='duration' type='range' min='0' max='28800' step='900' value='3600' icon='fas fa-stopwatch' title='Varighet p treningen' help='Hvor lenge var du ute? (HH:MI)' />

                                <InputNew stores={this.props.stores} field='speedAvg' icon='fas fa-tachometer-alt' title='Gjennomsnittsfart' help='Gjennomsnittsfart nr du var i bevegelse. Dersom du ikke vet dette s legger du bare inn distanse og varighet s blir dette regnet ut.' />

                                <div class='row px-0'>
                                    <div class='col-6'>
                                        <InputNew stores={this.props.stores} field='distanceKm' icon='fas fa-road' title='Distanse i km' help='Hvor lang var turen i km.' />
                                    </div>
                                    <div class='col-6'>
                                        <InputNew stores={this.props.stores} field='elevation' icon='fas fa-mountain' title='Hydemeter' help='Hydemeter du klatret p denne turen.' />
                                    </div>
                                </div>

                                <Equipments stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='number' field='equipment' icon='fas fa-sleigh' title='Utstyr' help='Utstyr som ble brukt. "trollvogn", "atv", "slede".' updateField={this.updateFieldValue} />

                                <InputNew stores={this.props.stores} field='comment' title='Kommentar' help='Kommentar til turen. Hvordan gikk det?' />
                                <InputNew stores={this.props.stores} field='weather' icon='fas fa-cloud-sun-rain' title='Vret p turen' help='Hvordan var vret.' />
                                <InputNew stores={this.props.stores} field='temperature' type='range' min='-30' max='20' step='1' icon='fas fa-temperature-low' title='Temperatur' help='Hvilken temperatur var det.' />

                                <Dogs stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='array' icon='fas fa-dog' elementtype='number' field='dogs' title='Hunder' help='Hvilke hunder var med p turen.' updateField={this.updateFieldValue} filter={(e) => (e.team === newWorkout.team || (newWorkout.dogs && newWorkout.dogs.indexOf(e.id) > -1))} />
                                <Mushers stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='array' elementtype='number' field='mushers' icon='fas fa-user' title='Kjrere' help='Hvem av kjrerne var med p turen.' updateField={this.updateFieldValue} />

                                <InputNew stores={this.props.stores} field='rating' type='range' min='0' max='6' step='1' icon='fas fa-dice' title='Rating av turen' help='Din rating av turen. 1-6' />

                                <button type='button' class='btn btn-primary'
                                    onClick={this.insertWorkout}
                                >
                                    Registrer ny treningstur
                                </button>
                            </div>
                        ) : (
                            <div>
                                <h5>Endre trening: {name}</h5>
                                <Teams stores={this.props.stores} saved={saved} workout={workout} type='number' field='team' icon='fas fa-users' title='Team' help='Teamet som er p tur.' updateField={this.updateFieldValue} />
                                <Types stores={this.props.stores} saved={saved} workout={workout} type='number' field='type' icon='fas fa-running' title='Type tur' help='Type treningstur. "trening", "konkurranse", "tur".' updateField={this.updateFieldValue} />
                                <Tracks stores={this.props.stores} saved={saved} workout={workout} type='number' field='track' icon='fas fa-map-marked' title='Runde' help='Hvilken treningsrunde er dette.' updateField={this.updateFieldValue} />

                                <Input saved={saved} workout={workout} field='name' icon='fas fa-heading' title='Tittel' help='Tittel p denne turen. "Fin hsttur i sende regn..."' onInput={this.updateField} />

                                <Input stores={this.props.stores} saved={saved} workout={workout} field='date' icon='fas fa-clock' type='date' min='2010-01-01' max='today' title='Dato' help='Dato for turen. Format p dato er: "YYYY-MM-DD".' onInput={this.updateField} updateField={this.updateFieldValue} />

                                <Input saved={saved} workout={workout} field='duration' type='range' min='0' max='28800' step='900' icon='fas fa-stopwatch' title='Varighet p treningen' help='Hvor lenge var du ute? (HH:MI)' onInput={this.updateField} />
                                <Input saved={saved} workout={workout} field='speedAvg' icon='fas fa-tachometer-alt' title='Gjennomsnittsfart' help='Gjennomsnittsfart nr du var i bevegelse. Dersom du ikke vet dette s legger du bare inn distanse og varighet s blir dette regnet ut.' onInput={this.updateField} />

                                <div class='row px-0'>
                                    <div class='col-6'>
                                        <Input saved={saved} workout={workout} field='distanceKm' icon='fas fa-road' title='Distanse i km' help='Hvor lang var turen i km.' onInput={this.updateField} />
                                    </div>
                                    <div class='col-6'>
                                        <Input saved={saved} workout={workout} field='elevation' icon='fas fa-mountain' title='Hydemeter' help='Hydemeter du klatret p denne turen.' onInput={this.updateField} />
                                    </div>
                                </div>

                                <Equipments stores={this.props.stores} saved={saved} workout={workout} type='number' field='equipment' icon='fas fa-sleigh' title='Utstyr' help='Utstyr som ble brukt. "trollvogn", "atv", "slede".' updateField={this.updateFieldValue} />

                                <Input saved={saved} workout={workout} field='comment' title='Kommentar' help='Kommentar til turen. Hvordan gikk det?' onInput={this.updateField} />
                                <Input saved={saved} workout={workout} field='weather' icon='fas fa-cloud-sun-rain' title='Vret p turen' help='Hvordan var vret.' onInput={this.updateField} />
                                <Input saved={saved} workout={workout} field='temperature' type='range' min='-30' max='20' step='1' icon='fas fa-temperature-low' title='Temperatur' help='Hvilken temperatur var det.' onInput={this.updateField} />

                                <Dogs stores={this.props.stores} saved={saved} workout={workout} type='array' elementtype='number' field='dogs' icon='fas fa-dog' title='Hunder' help='Hvilke hunder var med p turen.' updateField={this.updateFieldValue}  filter={(e) => (e.team === workout.team || workout.dogs.indexOf(e.id) > -1)} />
                                <Mushers stores={this.props.stores} saved={saved} workout={workout} type='array' elementtype='number' field='mushers' icon='fas fa-user' title='Kjrere' help='Hvem av kjrerne var med p turen.' updateField={this.updateFieldValue} />

                                <Input saved={saved} workout={workout} field='rating' type='range' min='0' max='6' step='1' icon='fas fa-dice' title='Rating av turen' help='Din rating av turen. 1-6' onInput={this.updateField} />

                                <Input saved={saved} workout={workout} field='image' type='image' title='Bilder' help='Legg til bilde fra treningen.' onInput={this.updateField} updateField={this.updateFieldValue} />
                            </div>                                                        
                        )}
					</div>

					{insertStatus && <div class='alert alert-success mt-4 col-12' role='alert'>
						Treningen er lagret.
					</div>}

                </div>

                <p class='mt-4'>
                    <i>
                        Start-stopp sporingen er ikke klar enn. Det er noen tekniske greier som Apple iOS har imot web-apper. 
                        Nr det gjelder Android, s trenger jeg en Android telefon jeg kan teste p.<br />
                        <br />
                        Det meste av utviklingen er styrt av dere brukere, s det er fint med tilbakemeldinger via Facebook.<br />
                    </i>
                </p>
                {!createNew && <div class='row'>
					<div class='offset-0 col-12 offset-sm-1 col-sm-10 offset-md-2 col-md-8 offset-lg-3 col-lg-6'>
                        {isTracking ? <button class='btn btn-danger' disabled={true}
                            onClick={workoutStore.stopTracking}
                        >Stopp sporing</button> : <button class='btn btn-success mr-3'  disabled={true}
                            onClick={workoutStore.startTracking}
                        >Start sporing</button>}
                    </div>
                    <div class='offset-0 col-12 offset-sm-1 col-sm-10 offset-md-2 col-md-8 offset-lg-3 col-lg-6'>
                        <xmp>{workoutLocations.map(() => '.')}</xmp>
                    </div>
				</div>}
			</div>
		);
	}
}

export default WorkoutsEdit;
