import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';

const initialState = {
    newMember: {},
};

@observer
class TeamsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadTeam() {
		const { teamStore, userStore } = this.props.stores;
        await teamStore.load(this.props.team, false, { addData: ['dogs'] });
        const { team } = teamStore;
        await userStore.getWorkoutSummary(team.id);
    }

    insertTeamMember = async () => {
        const { newMember } = this.state;
        const { teamStore } = this.props.stores;
		const { team } = teamStore;
        await teamStore.addMember(team.id, { ...newMember });
        this.setState({
            newMember: {},
        });
    }

    back = () => {
        route(`/teams/`, true);
    }

    componentWillMount() {
        this.loadTeam();
    }

	render() {
        const { newMember } = this.state;
		const { teamStore } = this.props.stores;
		const { team } = teamStore;

		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link' onClick={this.back}>
                            <i class='fas fa-arrow-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>Team - Legg til medlem</h5>
                    </div>
                    <div>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                    </div>
                </div>

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <h5>Legg til nytt medlem i {team.name}</h5>
                        <div class='form-group'>
                            <label for='emailInput'>E-post</label>
                            <input type='text' class='form-control' id='emailInput' aria-describedby='emailHelp'
                                onInput={linkState(this, 'newMember.email')}
                                value={newMember.email}
                            />
                            <small id='emailHelp' class='form-text text-muted'>E-postadressen til nytt medlem.</small>
                        </div>
                        <div class='alert alert-warning' role='alert'>
                            <i class='fas fa-exclamation-circle float-left mr-3' />
                            Medlemmer som legges til vil f full tilgang p hundene, treningene og lypene til dette teamet. <br />
                            <br />
                            Vi anbefaler at du kun legger til folk du stoler p :)<br />
                        </div>
                        <button type='button' class='btn btn-primary'
                            onClick={this.insertTeamMember}
                        >
                            Registrer nytt medlem
                        </button>

					</div>
				</div>
			</div>
		);
	}
}

export default TeamsDetail;
